import React from 'react';
import {Panel, PanelHeader} from '@vkontakte/vkui';
import { Button, Group, Div, Input, FormLayoutGroup } from '@vkontakte/vkui';

const Settings = props => {

    const addBalance = () => {
        console.log('yes');
    }

    return (
      <Panel id={props.id}>
            <PanelHeader>
                Настройки
		    </PanelHeader>
        <Group>
            <Div>
            <FormLayoutGroup top="Промо-код">
                <Input type="text" defaultValue="" />
                <Button size="xl" onClick={() => addBalance()}>
                    Ввести промо-код
                </Button>
            </FormLayoutGroup>
            </Div>
        </Group>
      </Panel>
    )
}

export default Settings;
